___CSS_LOADER_EXPORT___.push([module.id, ".mapa{height:600px}", "",{"version":3,"sources":["webpack://./resources/js/components/MapaNegocios.vue"],"names":[],"mappings":"AA2GA,MACA,YACA","sourcesContent":["<template>\r\n<div class=\"mapa\">\r\n  <l-map\r\n    :zoom=\"zoom\" \r\n    :center=\"center\" \r\n    :options=\"mapOptions\"\r\n  >\r\n      <l-tile-layer \r\n        :url=\"url\" \r\n        :attribution=\"attribution\"\r\n      />\r\n          <l-marker \r\n            v-for=\"negocio in negocios\" \r\n            v-bind:key=\"negocio.id\"\r\n            :lat-lng=\"obtenerCoordenadas(negocio)\"  \r\n            :icon=\"iconoNegocio(negocio)\" \r\n            @click=\"detallesNegocio(negocio.id)\" \r\n            >\r\n              <l-tooltip>\r\n                  <div class=\"text-muted font-weight-bold\">{{negocio.nombre_negocio}}</div>\r\n              </l-tooltip>\r\n          </l-marker>\r\n  </l-map>\r\n</div>\r\n</template>\r\n\r\n<script>\r\n\r\nimport { latLng } from \"leaflet\";\r\nimport { LMap, LTileLayer, LMarker, LTooltip, LIcon } from \"vue2-leaflet\";\r\n\r\n\r\nexport default {\r\n\r\n    components:{\r\n\r\n        LMap,\r\n        LTileLayer,\r\n        LMarker,\r\n        LTooltip,\r\n        LIcon\r\n\r\n\r\n    },\r\n    data() {\r\n        return {\r\n            zoom: 13,\r\n            center: latLng(23.137101780277284, -82.35785485661802),\r\n            url: \"https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png\",\r\n            attribution:\r\n                '&copy; <a href=\"http://osm.org/copyright\">OpenStreetMap</a> contributors',\r\n            currentZoom: 11.5,\r\n            currentCenter: latLng(23.137101780277284, -82.35785485661802),\r\n            showParagraph: false,\r\n            mapOptions: {\r\n                zoomSnap: 0.5\r\n            },\r\n            showMap: true\r\n        };\r\n    },\r\n    created(){\r\n        axios.get('/api/negocios')\r\n             .then(resp =>{\r\n                this.$store.commit('SHOW_ALL_NEGOCIOS',resp.data);\r\n             })\r\n             .catch(err=>{\r\n                console.log(err);\r\n             });\r\n    },\r\n    computed:{\r\n        negocios(){\r\n            return this.$store.getters.allNegocios;\r\n        }\r\n    },\r\n    methods:{\r\n        obtenerCoordenadas(negocio){\r\n            return{\r\n                lat:negocio.lat,\r\n                lng:negocio.lng\r\n            }\r\n        },\r\n        iconoNegocio(negocio){\r\n            const { slug } = negocio.categoria;\r\n            return L.icon({\r\n                iconUrl:`images/${slug}.png`,\r\n                iconSize:[35,35]\r\n            })\r\n        },\r\n        detallesNegocio(id){\r\n          return this.$router.push({ name:'negocios', params:{ id }});\r\n        }\r\n    },\r\n    watch:{\r\n        '$store.state.categoria':function(){\r\n            axios.get('/api/categoria/' + this.$store.getters.showCategoria)\r\n                 .then(resp=>{\r\n                    this.$store.commit(\"SHOW_ALL_NEGOCIOS\",resp.data);\r\n                 })\r\n                 .catch(err=>console.log(err)); \r\n        }\r\n    }   \r\n\r\n\r\n}\r\n</script>\r\n\r\n<style>\r\n .mapa{\r\n    height: 600px;\r\n }\r\n</style>"],"sourceRoot":""}]);
